.sidebar {
  position: fixed;
  top: 24px;
  left: 0;
  width: 18%;
  height: 100%;
  transition: all 0.3s ease-out;
  z-index: 20;
  background-color: white;
  overflow: auto;

  .title {
    display: flex;
    justify-content: space-between;
    align-items: center;
    border-bottom: 1px solid var(--light-secondary-color);
    padding: 1rem;
    img {
      width: 50%;
    }
    i {
      color: var(--secondary-color);
      font-size: 1.5rem;
      &:hover {
        color: var(--primary-color);
      }
    }
  }
  .menu {
    padding: 1rem;
    .header {
      display: flex;
      justify-content: space-between;
      align-items: center;
      margin-bottom: 1rem;
      outline: none;

      span {
        color: #212121;
        font-weight: 700;
      }
      i {
        color: var(--secondary-color);
        cursor: pointer;
      }
    }
    .subHeader {
      display: flex;
      justify-content: space-between;
      align-items: center;
      margin: 1rem 0 1rem 1rem;
      span {
        color: var(--secondary-color);
      }
      i {
        color: var(--secondary-color);
        cursor: pointer;
      }
    }
    .baseItem {
      span {
        display: block;
        margin: 1rem 0 0.5rem 2rem;
        color: var(--secondary-color);
        font-size: 14px;
        cursor: pointer;
        &:hover {
          color: var(--primary-color);
        }
      }
    }
  }
}

.accordion__item {
  display: none;
}
.accordion__heading {
  display: none;
}

.Open {
  transform: translateX(0);
}
.Close {
  transform: translateX(-100%);
}

@media screen and (max-width:1000px){
  .sidebar{
    width: 100%;
  }
}