.branches {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  width: 100%;
  background-color: var(--primary-color);
  .description {
    background-color: var(--primary-color);
    width: 100%;
    height: 7rem;
    display: flex;
    justify-content: center;
    align-items: center;
    span {
      display: block;
      text-align: center;
      color: var(--white-color);
      font-size: 1.5rem;
    }
  }
  .viewBranches {
    width: 70%;
    background: linear-gradient(
      90deg,
      rgba(127, 1, 24, 1) 0%,
      rgba(204, 2, 38, 1) 100%
    );
    clip-path: polygon(4% 0, 100% 0%, 100% 100%, 0% 100%);
    display: flex;
    align-items: center;
    justify-content: center;
    height: 7rem;

    .btn {
      display: flex;
      align-items: center;
      justify-content: center;
      width: 12rem;
      height: 2.5rem;
      border-radius: 10px;
      border: none;
      background-color: var(--dark-white-color);
      font-weight: 600;
      outline: none;
      cursor: pointer;
      
      svg {
        width: 1.5rem;
        margin-right: 0.5rem;
      }
    }
  }
}


@media screen and (max-width:800px){
  .branches{
    flex-direction: column;
    .viewBranches{
      width: 100%;
      clip-path: none;
    }
  }
}