.showcase {
  outline: none;
  @media screen and (max-width:800px){
    margin-top: 8rem;
  }
}

.next {
  outline: none;
  transform: translateX(-200%);
  width: 3rem;
  font-size: 1.2rem;
  color: #d5293f !important;
  font-weight: bold;
  background-color: rgba(250, 249, 247, 0.4) !important;
  height: 3rem;
  border-radius: 50%;
}

.prev {
  outline: none;
  transform: translateX(200%);
  width: 3rem;
  font-size: 1.2rem;
  color: var(--primary-color) !important;
  font-weight: bold;
  background-color: rgba(250, 249, 247, 0.4) !important;
  height: 3rem;
  border-radius: 50%;
}

.pagingDots {
  fill: #6b6b6b !important;
  outline: none;
  border-radius: 50%;
  margin: 0.5rem;
  background-color:var(--secondary-color) !important;
  transform: scale(0.8);

  &:hover , &:active {
    fill: var(--primary-color) !important;
    background-color: var(--primary-color) !important;
  }
}

@media screen and (max-width:800px){
  .next{
    width: 2rem;
    height: 2rem;
    font-size: .8rem;
  }
  .prev{
    width: 2rem;
    height: 2rem;
    font-size: .8rem;
  }
  }
@media screen and (max-width:500px){
  .next{
    width: 1rem;
    height: 1rem;
    font-size: .7rem;
  }
  .prev{
    width: 1rem;
    height: 1rem;
    font-size: .7rem;
  }
  }