.sidebar {
  position: fixed;
  top: 5.5px;
  right: 0;
  width: 18%;
  height: 100%;
  transition: all 0.3s ease-out;
  z-index: 20;
  background-color: white;
  overflow: auto;

  .title {
    display: flex;
    justify-content: space-between;
    align-items: center;
    border-bottom: 1px solid var(--light-secondary-color);
    padding: 1rem;

    .cart {
      svg {
        margin-right: 1rem;
        transform: translateY(0.5rem);
        color: var(--primary-color);
        font-size: 1.5rem;
      }
      span {
        font-size: 1.4rem;
        font-weight: 700;
      }
    }
    i {
      color: var(--secondary-color);
      font-size: 1.5rem;
      &:hover {
        color: var(--primary-color);
      }
    }
  }
  .redBtn {
    display: block;
    width: 90%;
    margin: 1rem auto;
    height: 2rem;
    border: none;
    border-radius: 5px;
    background-color: var(--primary-color);
    color: white;
    font-weight: 700;
  }
}

.message {
  text-align: center;
  font-weight: 700;
  font-size: 1rem;
}

.Open {
  transform: translateX(0);
}
.Close {
  transform: translateX(100%);
}
@media screen and (max-width:1000px){
  .sidebar{
    width: 100%;
  }
}